import { Fragment, useRef } from "react";

import Container from "@mui/material/Container";

import Grid from "@mui/material/Grid";
import Stack from "@mui/material/Stack";
import Box from "@mui/material/Box";
import PropTypes from "prop-types";
import useScrollTrigger from "@mui/material/useScrollTrigger";
import Fab from "@mui/material/Fab";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import Fade from "@mui/material/Fade";

import NavBar from "./NavBar";
import Summary from "./Summary";
import ParticipantInfo from "./ParticipantInfo";
import DomainLevelCard from "./DomainLevelCard";
import InstrumentLevelCard from "./InstrumentLevelCard";

import appData from "../../data/appData.json";

import { getParticipant } from "../../helpers/getParticipant";
import { getReportData } from "../../helpers/getReportData";
import { Typography } from "@mui/material";

function ScrollTop(props) {
  const { children } = props;
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window,
    disableHysteresis: true,
    threshold: 100,
  });

  const handleClick = () => {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
  };

  return (
    <Fade in={trigger}>
      <Box
        onClick={handleClick}
        role="presentation"
        sx={{ position: "fixed", bottom: 16, right: 16 }}
      >
        {children}
      </Box>
    </Fade>
  );
}

ScrollTop.propTypes = {
  children: PropTypes.element.isRequired,
};

function MainProgram(props) {
  window.scrollTo({
    top: 0,
    left: 0,
  });

  const { queryObject } = props;
  const participant = getParticipant(queryObject, appData);

  const reportData = getReportData(participant, appData);

  const scrollToRef = (ref) => {
    ref.current.scrollIntoView({ behavior: "smooth" });
  };

  const executiveRef = useRef();
  const memoryRef = useRef();
  const languageRef = useRef();
  const tmtaRef = useRef();
  const tmtbRef = useRef();
  const ldstRef = useRef();
  const ravl1Ref = useRef();
  const il_lettersRef = useRef();
  const il_ldpRef = useRef();
  const nsbRef = useRef();
  const benson2Ref = useRef();
  const ravl2Ref = useRef();
  const ravl3Ref = useRef();
  const craft1vRef = useRef();
  const craft1pRef = useRef();
  const craft2vRef = useRef();
  const craft2pRef = useRef();
  const vffRef = useRef();
  const vflRef = useRef();
  const animalsRef = useRef();
  const vegetablesRef = useRef();
  const mintRef = useRef();

  const refCollection = useRef({
    executiveRef,
    memoryRef,
    languageRef,
    tmtaRef,
    tmtbRef,
    ldstRef,
    ravl1Ref,
    il_lettersRef,
    il_ldpRef,
    nsbRef,
    benson2Ref,
    ravl2Ref,
    ravl3Ref,
    craft1vRef,
    craft1pRef,
    craft2vRef,
    craft2pRef,
    vffRef,
    vflRef,
    animalsRef,
    vegetablesRef,
    mintRef,
  });

  const refObject = {
    executiveRef,
    memoryRef,
    languageRef,
    tmtaRef,
    tmtbRef,
    ldstRef,
    ravl1Ref,
    il_lettersRef,
    il_ldpRef,
    nsbRef,
    benson2Ref,
    ravl2Ref,
    ravl3Ref,
    craft1vRef,
    craft1pRef,
    craft2vRef,
    craft2pRef,
    vffRef,
    vflRef,
    animalsRef,
    vegetablesRef,
    mintRef,
  };

  const renderExecutiveInstrumentCards = () => {
    return ["tmta", "tmtb", "ldst"].map((instrument) => {
      return (
        <Grid item xs={12} ref={refObject[`${instrument}Ref`]} key={instrument}>
          <InstrumentLevelCard
            instrumentName={instrument}
            reportData={reportData}
            participant={participant}
            domainLabel={"Executive"}
          ></InstrumentLevelCard>
        </Grid>
      );
    });
  };

  const renderMemoryCards = () => {
    return [
      "nsb",
      "il_letters",
      "il_ldp",
      "benson2",
      "ravl1",
      "ravl2",
      "ravl3",
      "craft1v",
      "craft1p",
      "craft2v",
      "craft2p",
    ].map((instrument) => {
      return (
        <Grid item xs={12} ref={refObject[`${instrument}Ref`]} key={instrument}>
          <InstrumentLevelCard
            instrumentName={instrument}
            reportData={reportData}
            participant={participant}
            domainLabel={"Memory"}
          ></InstrumentLevelCard>
        </Grid>
      );
    });
  };

  const renderLanguageCards = () => {
    return ["animals", "vegetables", "mint", "vff", "vfl"].map((instrument) => {
      return (
        <Grid item xs={12} ref={refObject[`${instrument}Ref`]} key={instrument}>
          <InstrumentLevelCard
            instrumentName={instrument}
            reportData={reportData}
            participant={participant}
            domainLabel={"Language"}
          ></InstrumentLevelCard>
        </Grid>
      );
    });
  };

  return (
    <Fragment>
      <NavBar></NavBar>
      <Container>
        <Stack spacing={1} sx={{ marginTop: 2 }}>
          <Grid container spacing={1}>
            <Grid item xs={12}>
              <Summary
                reportData={reportData}
                refCollection={refCollection}
                scrollToRef={scrollToRef}
              />
            </Grid>
            <Grid item xs={12}>
              <ParticipantInfo participant={participant}></ParticipantInfo>
            </Grid>

            <Grid item xs={12} ref={executiveRef}>
              <DomainLevelCard
                reportData={reportData}
                domainName={"executive"}
              ></DomainLevelCard>
            </Grid>

            <Grid item xs={12} ref={memoryRef}>
              <DomainLevelCard
                reportData={reportData}
                domainName={"memory"}
              ></DomainLevelCard>
            </Grid>

            <Grid item xs={12} ref={languageRef}>
              <DomainLevelCard
                reportData={reportData}
                domainName={"language"}
              ></DomainLevelCard>
            </Grid>

            <Grid item xs={12}>
              <Typography variant="h3">Executive Tests</Typography>
            </Grid>

            {renderExecutiveInstrumentCards()}

            <Grid item xs={12}>
              <Typography variant="h3">Memory Tests</Typography>
            </Grid>

            {renderMemoryCards()}

            <Grid item xs={12}>
              <Typography variant="h3">Language Tests</Typography>
            </Grid>

            {renderLanguageCards()}

            <Grid item xs={12}>
              <Typography variant="h3">Visuospatial Tests</Typography>
            </Grid>

            <Grid item xs={12}>
              <InstrumentLevelCard
                instrumentName="benson1"
                reportData={reportData}
                participant={participant}
                domainLabel={"Visuospatial"}
              ></InstrumentLevelCard>
            </Grid>
          </Grid>
        </Stack>
      </Container>
      <ScrollTop {...props}>
        <Fab size="small" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </Fragment>
  );
}

export default MainProgram;
