import getInstrumentChartData from "./reportDataHelpers/instChartData";
import getReport from "./reportDataHelpers/cleanReport";
import getResidualPlotData from "./reportDataHelpers/resVizData";

const getReportData = (participant, appData) => {
  // Generate report for participant
  const report = getReport(participant, appData);

  // Combine participant data with instrument chart data
  const instrumentChartData = getInstrumentChartData(participant, appData);

  // Combine report data with residual plot data
  const residualPlotData = getResidualPlotData(report, appData);

  return { report, instrumentChartData, residualPlotData };
};

export { getReportData };
