import React, { useState } from "react";

import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Button from "@mui/material/Button";
import Stack from "@mui/material/Stack";
import TextField from "@mui/material/TextField";

import { getQueryObject } from "../../helpers/getQueryObject";
import { Typography } from "@mui/material";

function TextInputCard({ setQueryObject }) {
  const [textInput, setTextInput] = useState("");

  const onSubmitTextInput = (textInput) => {
    setQueryObject(getQueryObject(textInput));
  };

  return (
    <Card>
      <CardContent>
        <Stack spacing={2}>
          <Typography variant="h5">Text Input Card</Typography>

          <Box component="form" noValidate autoComplete="off">
            <TextField
              id="filled-multiline-static"
              label="Enter string of data here..."
              multiline
              rows={6}
              fullWidth
              onInput={(e) => setTextInput(e.target.value)}
              value={textInput}
            />
          </Box>
          <Stack direction="column" alignItems="flex-end">
            <Button
              size="small"
              onClick={() => {
                onSubmitTextInput(textInput);
              }}
            >
              Submit Text
            </Button>
          </Stack>
        </Stack>
      </CardContent>
    </Card>
  );
}

export default TextInputCard;
