// Imports from the Material-UI library

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";

// Custom Components
import OverallClassificationCard from "./OverallClassificationCard";
import DomainCard from "./DomainCard";
import ExecutiveTests from "./ExecutiveTests";
import MemoryTests from "./MemoryTests";
import LanguageTests from "./LanguageTests";

// The main "App" component
export default function App({ reportData, scrollToRef, refCollection }) {
  const { report } = reportData;
  const { residualPlotData } = reportData;
  const { domainLevelZScores, instrumentLevelZScores } = report;
  const classification = report["classifications"]["pooled"];

  const { executiveRef, memoryRef, languageRef } = refCollection.current;

  return (
    <Card>
      <CardContent>
        <Stack spacing={1}>
          <OverallClassificationCard
            classification={classification}
          ></OverallClassificationCard>

          <Stack direction="row" spacing={1}>
            <DomainCard
              nameOfDomain={"executive"}
              labelForDomain={"Executive Domain"}
              domainLevelZScores={domainLevelZScores}
              residualPlotData={residualPlotData}
              cardHeight={100}
              cardRelativeWidth={1 / 3}
              plotHeight={70}
              plotWidth={150}
              marginTop={25}
              marginLeft={10}
              marginRight={10}
              marginBottom={20}
              domainRef={executiveRef}
              scrollToRef={scrollToRef}
            ></DomainCard>

            <DomainCard
              nameOfDomain={"memory"}
              labelForDomain={"Memory Domain"}
              domainLevelZScores={domainLevelZScores}
              residualPlotData={residualPlotData}
              cardHeight={100}
              cardRelativeWidth={1 / 3}
              plotHeight={70}
              plotWidth={150}
              marginTop={25}
              marginLeft={10}
              marginRight={10}
              marginBottom={20}
              domainRef={memoryRef}
              scrollToRef={scrollToRef}
            ></DomainCard>
            <DomainCard
              nameOfDomain={"language"}
              labelForDomain={"Language Domain"}
              domainLevelZScores={domainLevelZScores}
              residualPlotData={residualPlotData}
              cardHeight={100}
              cardRelativeWidth={1 / 3}
              plotHeight={70}
              plotWidth={150}
              marginTop={25}
              marginLeft={10}
              marginRight={10}
              marginBottom={20}
              domainRef={languageRef}
              scrollToRef={scrollToRef}
            ></DomainCard>
          </Stack>
          <Stack direction="row" spacing={1}>
            <ExecutiveTests
              residualPlotData={residualPlotData}
              instrumentLevelZScores={instrumentLevelZScores}
              refCollection={refCollection}
              scrollToRef={scrollToRef}
            ></ExecutiveTests>
            <MemoryTests
              residualPlotData={residualPlotData}
              instrumentLevelZScores={instrumentLevelZScores}
              refCollection={refCollection}
              scrollToRef={scrollToRef}
            ></MemoryTests>
            <LanguageTests
              residualPlotData={residualPlotData}
              instrumentLevelZScores={instrumentLevelZScores}
              refCollection={refCollection}
              scrollToRef={scrollToRef}
            ></LanguageTests>
          </Stack>
        </Stack>
      </CardContent>
    </Card>
  );
}
