import * as d3 from "d3";

const drawResidualRidgelines = (svg, residualPlotData) => {
  const { width, height, yBuffer } = residualPlotConfig;
  const { ridgelineData, zScores } = residualPlotData;
  const { x, y, xAxis, yAxis, area, line } = setupRidgelines(ridgelineData);

  svg.selectAll("*").remove();

  svg.attr("height", height);
  svg.attr("width", width);

  const xAxisGroup = svg.append("g");
  const yAxisGroup = svg.append("g");

  xAxisGroup.call(xAxis);
  yAxisGroup.call(yAxis);

  drawRidgelines(svg, ridgelineData, y, area, line, zScores);
  addMarkers(svg, zScores, x, y, yBuffer);
};

const drawRidgelines = (svg, ridgelineData, y, area, line) => {
  // Create a plotGroup that will let us iterate over each series
  const plotGroup = svg
    .append("g")
    .selectAll("g")
    .data(ridgelineData.series)
    .join("g")
    .attr("transform", (d) => `translate(0,${y(d.name) + 1})`);

  // Shade in Density for each series

  plotGroup
    .append("path")
    .attr("fill", (d) => "#ddd")
    .attr("d", (d) => area(d.values));

  // Draw Path for Each Series
  plotGroup
    .append("path")
    .attr("fill", "none")
    .attr("stroke", "black")
    .attr("d", (d) => line(d.values));
};

const residualPlotConfig = {
  width: 350,
  height: 275,
  overlap: 0.8,
  margin: { top: 60, right: 20, bottom: 30, left: 120 },
  yBuffer: 10,
};

const setupRidgelines = (ridgelineData) => {
  const { width, height, overlap, margin } = residualPlotConfig;
  const x = d3
    .scaleLinear()
    .domain([-4, 4])
    .range([margin.left, width - margin.right]);

  const y = d3
    .scalePoint()
    .domain(ridgelineData.series.map((d) => d.name))
    .range([margin.top, height - margin.bottom]);

  const z = d3
    .scaleLinear()
    .domain([0, d3.max(ridgelineData.series, (d) => d3.max(d.values))])
    .nice()
    .range([0, -overlap * y.step()]);

  const xAxis = (g) =>
    g.attr("transform", `translate(0,${height - margin.bottom})`).call(
      d3
        .axisBottom(x)
        .ticks(width / 80)
        .tickSizeOuter(0)
    );

  const yAxis = (g) =>
    g
      .attr("transform", `translate(${margin.left},0)`)
      .call(d3.axisLeft(y).tickSize(0).tickPadding(4))
      .call((g) => g.select(".domain").remove());

  const area = d3
    .area()
    .curve(d3.curveBasis)
    .defined((d) => !isNaN(d))
    .x((d, i) => x(ridgelineData.dates[i]))
    .y0(0)
    .y1((d) => z(d));

  const line = area.lineY1();

  return { x, y, z, xAxis, yAxis, area, line };
};

const addMarkers = (svg, zScores, x, y, yBuffer) => {
  const lineGroup = svg.append("g");
  const pointGroup = svg.append("g");

  const studies = ["pooled", "ARIC", "NACC", "GMBI", "MCSA"];

  const zScoresForPlot = [];
  for (const study of studies) {
    const zScore = zScores[study];

    if (zScore) {
      zScoresForPlot.push({ zScore, study });
    }
  }

  pointGroup
    .selectAll("circle")
    .data(zScoresForPlot)
    .join("circle")
    .attr("cx", (d) => x(d.zScore))
    .attr("cy", (d) => y(d.study) - y.step() + yBuffer)
    .attr("r", 2)
    .attr("fill", "steelblue")
    .attr("stroke", "black")
    .attr("opacity", 1)
    .attr("stroke-width", 1);

  lineGroup
    .selectAll("line")
    .data(zScoresForPlot)
    .join("line")
    .attr("x1", (d) => x(d.zScore))
    .attr("x2", (d) => x(d.zScore))
    .attr("y1", (d) => y(d.study) - y.step() + yBuffer)
    .attr("y2", (d) => y(d.study))
    .attr("stroke-width", 1)
    .style("stroke", "black") // colour the line
    .style("stroke-dasharray", "2");
};

export default drawResidualRidgelines;
