import React from "react";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import CardActions from "@mui/material/CardActions";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { blue, purple } from "@mui/material/colors";
import { styled } from "@mui/material/styles";

import { useDispatch } from "react-redux";

const theme = createTheme({
  palette: {
    background: {
      paper: blue[600],
    },
  },
});

const ColorButton = styled(Button)(({ theme }) => ({
  color: theme.palette.getContrastText(blue[500]),
  backgroundColor: blue[500],
  "&:hover": {
    backgroundColor: blue[300],
  },
}));

function ExampleStringOfDataCard() {
  const dispatch = useDispatch();

  const exampleInputString = `age=75,black=0,female=0,edu3cat=1,ravl1=30,ravl1_result=0,benson1=16,benson1_result=0,
ldst=28,ldst_result=0,animals=20,animals_result=0,il_letters=0,il_letters_result=2,
il_ldp=0,il_ldp_result=2,vegetables=15,vegetables_result=0,nsb=8,nsb_result=0,
tmta=41,tmta_result=0,tmtb=216,tmtb_result=0,benson2=7,benson2_result=0,
ravl2=3,ravl2_result=0,ravl3=18,ravl3_result=0,craft1v=18,craft1v_result=0,
craft1p=15,craft1p_result=0,mint=26,mint_result=0,vff=10,
vff_result=0,vfl=10,vfl_result=0,craft2v=24,craft2v_result=0,craft2p=17,craft2p_result=0`;

  return (
    <ThemeProvider theme={theme}>
      <Card>
        <CardContent>
          <Stack spacing={1}>
            <Typography variant="h5" color="common.white">
              Example String of Data
            </Typography>
            <Typography variant="caption" color="common.white">
              <code>{exampleInputString}</code>
            </Typography>
          </Stack>
          <CardActions disableSpacing={true}>
            <ColorButton
              sx={{ marginLeft: -1, marginTop: 1 }}
              size="small"
              onClick={() => {
                navigator.clipboard.writeText(exampleInputString);
                dispatch({
                  type: "SET_SNACKBAR_MESSAGE",
                  payload: "Example String of Data copied to clipboard",
                });
                dispatch({
                  type: "OPEN_SNACKBAR",
                });
              }}
            >
              Copy to clipboard
            </ColorButton>
          </CardActions>
        </CardContent>
      </Card>
    </ThemeProvider>
  );
}

export default ExampleStringOfDataCard;
