import React from "react";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import { createTheme, ThemeProvider, styled } from "@mui/material/styles";
import ListItemText from "@mui/material/ListItemText";

const theme = createTheme({
  padding: 20,
});

const FireNav = styled(List)({
  "& .MuiListItem-root": {
    paddingTop: 6,
  },
});

const renderZScoreItem = (zScore) => {
  if (!zScore) {
    return <span>N/A</span>;
  }
  if (zScore < -1.5) {
    return <span>{Math.round(100 * zScore) / 100}</span>;
  } else if (zScore < -0.5) {
    return <span>{Math.round(100 * zScore) / 100}</span>;
  } else {
    return <span>{Math.round(100 * zScore) / 100}</span>;
  }
};

const ListOfZScores = (props) => {
  const { zScores } = props;
  const { ARIC, GMBI, MCSA, NACC, pooled } = zScores;
  return (
    <ThemeProvider theme={theme}>
      <FireNav sx={{ marginTop: 3 }}>
        <ListItem>
          <ListItemText>POOLED: {renderZScoreItem(pooled)}</ListItemText>
        </ListItem>

        <ListItem>
          <ListItemText>ARIC: {renderZScoreItem(ARIC)}</ListItemText>
        </ListItem>

        <ListItem>
          <ListItemText>GMBI: {renderZScoreItem(GMBI)}</ListItemText>
        </ListItem>

        <ListItem>
          <ListItemText>NACC: {renderZScoreItem(NACC)}</ListItemText>
        </ListItem>

        <ListItem>
          <ListItemText>MCSA: {renderZScoreItem(MCSA)}</ListItemText>
        </ListItem>
      </FireNav>
    </ThemeProvider>
  );
};

export default ListOfZScores;
