import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";

import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

const ParticipantInfo = (props) => {
  const { participant } = props;
  const { demographics } = participant;
  const { age, female, black, Hs, HsPlus } = demographics;

  let sex;
  if (female === 1) {
    sex = "Female";
  } else if (female === 0) {
    sex = "Male";
  }

  let race;
  if (black === 1) {
    race = "Black / African American";
  } else if (black === 0) {
    race =
      "Either White / Caucasian, Asian, American Indian or Alaskan Native, Native Hawaiian or Other Pacific Islander, or Other";
  }

  let education;
  if (Hs === 1) {
    education = "High School";
  } else if (HsPlus === 1) {
    education = "Greater than High School";
  } else {
    education = "Less than High School";
  }
  return (
    <Card>
      <CardContent>
        <Stack direction="column" alignItems="center" spacing={1}>
          <Typography variant="h4">Participant Information</Typography>

          <div className="row">
            <div className="col s6 offset-s3">
              <div className={`card`} id="classification-card">
                <div className="card-content">
                  <TableContainer component={Paper}>
                    <Table aria-label="simple table">
                      <TableBody>
                        <TableRow>
                          <TableCell>
                            <b>Age (Years)</b>
                          </TableCell>
                          <TableCell>{age}</TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>
                            <b>Sex</b>
                          </TableCell>
                          <TableCell>{sex}</TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>
                            <b>Race</b>
                          </TableCell>
                          <TableCell>{race}</TableCell>
                        </TableRow>
                        <TableRow>
                          <TableCell>
                            <b>Education</b>
                          </TableCell>
                          <TableCell>{education}</TableCell>
                        </TableRow>
                      </TableBody>
                    </Table>
                  </TableContainer>
                </div>
              </div>
            </div>
          </div>
        </Stack>
      </CardContent>
    </Card>
  );
};

export default ParticipantInfo;
