import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";

import appData from "../../data/appData.json";
import ResidualRidgelinesViz from "./ResidualRidgelinesViz";
import ListOfZScores from "./ListOfZScores";

export default function BasicCard({ reportData, domainName }) {
  const { report, residualPlotData } = reportData;
  const { domainLevelZScores } = report;
  const zScores = domainLevelZScores[domainName];

  const { numberOfInstrumentsInDomainPerStudy, studies } = appData;
  const numInstrumentsPerStudy = numberOfInstrumentsInDomainPerStudy.filter(
    (d) => d.domain === domainName
  );

  const numInstruments = {};
  for (const study of studies) {
    numInstruments[study] = numInstrumentsPerStudy.find(
      (d) => d.study === study
    )["N"];
  }

  const domainNameLabel = appData.instrumentMetadata.find(
    (d) => d.domainName === domainName
  )["domain"];

  const resPlotData = residualPlotData[domainName];
  return (
    <Card sx={{ minWidth: 275 }}>
      <CardContent>
        <Typography variant="h4">{domainNameLabel} zScores</Typography>
        <Typography variant="h5">by Study</Typography>
        <Stack direction="row" spacing={2} justifyContent="center">
          <ListOfZScores zScores={zScores}></ListOfZScores>
          <ResidualRidgelinesViz
            resPlotData={resPlotData}
          ></ResidualRidgelinesViz>
        </Stack>
      </CardContent>
    </Card>
  );
}
