const getReport = (participant, appData) => {
  const instrumentLevelZScores = getInstrumentLevelZScores(
    participant,
    appData
  );
  const domainLevelZScores = getDomainLevelZScores(
    instrumentLevelZScores,
    appData,
    participant
  );
  const numFailedDomains = getNumberOfFailedDomains(
    domainLevelZScores,
    appData
  );
  const classifications = getClassifications(numFailedDomains, appData);

  return {
    instrumentLevelZScores,
    domainLevelZScores,
    numFailedDomains,
    classifications,
  };
};

const getModel = (instrument, study, appData) => {
  const { instrumentLevelModels, instrumentMetadata } = appData;

  const modelName = instrumentMetadata.find((d) => d.test === instrument)[
    "modelName"
  ];

  if (modelName) {
    return instrumentLevelModels.find(
      (datum) => (datum.study === study) & (datum.instrument === modelName)
    );
  } else {
    return null;
  }
};

const getObservedScore = (participant, instrumentName, appData) => {
  const { instrumentMetadata } = appData;

  const modelName = instrumentMetadata.find((d) => d.test === instrumentName)[
    "modelName"
  ];

  const rawScore = participant["modelScores"][modelName];

  return rawScore;
};

const getPrediction = (model, participant, appData) => {
  const { colNames } = appData;

  let sum = model["intercept"];

  for (const colName of colNames) {
    sum += participant["demographics"][colName] * model[colName];
  }

  return sum;
};

const getZScore = (participant, instrument, study, appData) => {
  if ((participant.demographics.black === 1) & (study === "MCSA")) {
    return null;
  }

  const { missing } = participant;
  if (missing.includes(instrument)) {
    return -4;
  }

  if (instrument === "tmta") {
    const rawScore = participant["rawScores"]["tmta"];

    if (rawScore === 240) {
      return -4;
    }
  }

  if (instrument === "tmtb") {
    const rawScore = participant["rawScores"]["tmtb"];

    if (rawScore === 300) {
      return -4;
    }
  }

  const observed = getObservedScore(participant, instrument, appData);

  if (isNaN(observed)) {
    const zScore = -4;
    return zScore;
  }
  const model = getModel(instrument, study, appData);

  if (model) {
    const predicted = getPrediction(model, participant, appData);
    const zScore = (observed - predicted) / model["RMSE"];
    return zScore;
  } else {
    return null;
  }
};

const getOneBatchOfInstrumentLevelZScores = (
  participant,
  instrument,
  appData
) => {
  const { studies } = appData;

  const zScores = {};

  for (const study of studies) {
    const zScore = getZScore(participant, instrument, study, appData);
    zScores[study] = zScore;
  }
  return zScores;
};

const getInstrumentLevelZScores = (participant, appData) => {
  const { instruments } = appData;

  const instrumentLevelZScores = {};

  for (const instrument of instruments) {
    instrumentLevelZScores[instrument] = getOneBatchOfInstrumentLevelZScores(
      participant,
      instrument,
      appData
    );
  }

  return instrumentLevelZScores;
};

const getObservedMeanForDomainAndStudy = (
  instrumentLevelZScores,
  domain,
  study,
  appData
) => {
  const { instrumentMetadata } = appData;

  const instrumentsInDomain = instrumentMetadata
    .filter((d) => d.domainName === domain)
    .map((d) => d.test);

  let numScoresObserved = 0;
  let sumOfScores = 0;

  for (const instrument of instrumentsInDomain) {
    // get the zScore for this instrument/study combo

    const instZScores = instrumentLevelZScores[instrument];

    const zScoreForStudy = instZScores[study];

    if (zScoreForStudy) {
      numScoresObserved += 1;
      sumOfScores += zScoreForStudy;
    }
  }

  if (numScoresObserved > 0) {
    return sumOfScores / numScoresObserved;
  } else {
    return null;
  }
};

const getDomainLevelZScoreForDomainAndStudy = (
  instrumentLevelZScores,
  domain,
  study,
  appData,
  participant
) => {
  if ((participant.demographics.black === 1) & (study === "MCSA")) {
    return null;
  }
  const { domainLevelModels } = appData;
  const modelInfo = domainLevelModels.find(
    (d) => (d.study === study) & (d.domain === domain)
  );
  if (modelInfo) {
    const expectedMean = modelInfo.mean;
    const sd = modelInfo.sd;
    const observedMean = getObservedMeanForDomainAndStudy(
      instrumentLevelZScores,
      domain,
      study,
      appData
    );
    return (observedMean - expectedMean) / sd;
  } else {
    return null;
  }
};

const getDomainLevelZScores = (
  instrumentLevelZScores,
  appData,
  participant
) => {
  const { domains, studies } = appData;

  const results = {};

  for (const domain of domains) {
    const resultsForDomain = {};

    for (const study of studies) {
      resultsForDomain[study] = getDomainLevelZScoreForDomainAndStudy(
        instrumentLevelZScores,
        domain,
        study,
        appData,
        participant
      );
    }

    results[domain] = resultsForDomain;
  }

  return results;
};

const getNumberOfFailedDomainsForStudy = (
  domainLevelZScores,
  study,
  appData
) => {
  const { domains } = appData;
  let numberFailed = 0;
  for (const domain of domains) {
    const zScore = domainLevelZScores[domain][study];
    if (zScore) {
      if ((zScore < -1.5) & (domain !== "visuospatial")) {
        numberFailed += 1;
      }
    }
  }
  return numberFailed;
};

const getNumberOfFailedDomains = (domainLevelZScores, appData) => {
  const { studies } = appData;
  const results = {};
  for (const study of studies) {
    results[study] = getNumberOfFailedDomainsForStudy(
      domainLevelZScores,
      study,
      appData
    );
  }
  return results;
};

const getClassificationForStudy = (numFailedDomains, study) => {
  const num = numFailedDomains[study];
  if (num === 0) {
    return "Normal";
  } else if (num === 1) {
    return "MCI";
  } else {
    return "Dementia";
  }
};

const getClassifications = (numFailedDomains, appData) => {
  const { studies } = appData;
  const results = {};
  for (const study of studies) {
    results[study] = getClassificationForStudy(numFailedDomains, study);
  }
  return results;
};

export default getReport;
