import { useRef, useEffect, useState } from "react";
import { select } from "d3";

import { useDispatch } from "react-redux";

import drawDiscreteInstrument from "../../helpers/visualizations/drawDiscreteInstrument";

const DiscreteInstrumentViz = (props) => {
  const dispatch = useDispatch();
  const [clickCount, setCount] = useState(0);

  const handleClick = () => {
    setCount(clickCount + 1);
  };

  const { discreteInstrumentPlotData } = props;

  const svgRef = useRef();

  useEffect(() => {
    const svg = select(svgRef.current);
    drawDiscreteInstrument(
      svg,
      discreteInstrumentPlotData,
      clickCount,
      dispatch
    );
  }, [discreteInstrumentPlotData, clickCount, dispatch]);

  return <svg ref={svgRef} onClick={handleClick}></svg>;
};

export default DiscreteInstrumentViz;
