const getParticipant = (queryObject, appData) => {
  const cleanParticipant = {};

  cleanParticipant["demographics"] = getDemographics(queryObject);
  cleanParticipant["rawScores"] = getRawScores(queryObject, appData);
  cleanParticipant["modelScores"] = getModelScores(queryObject, appData);
  cleanParticipant["missing"] = getMissing(queryObject, appData);

  return cleanParticipant;
};

export { getParticipant };

/**
 *
 * Helpers
 */

const getDemographics = (queryObject) => {
  const demographics = {};

  for (const colName of ["age", "black", "female"]) {
    demographics[colName] = queryObject[colName];
  }

  queryObject["edu3cat"] === 1
    ? (demographics["Hs"] = 1)
    : (demographics["Hs"] = 0);

  queryObject["edu3cat"] === 2
    ? (demographics["HsPlus"] = 1)
    : (demographics["HsPlus"] = 0);

  demographics["blackXHs"] = demographics["black"] * demographics["Hs"];

  demographics["blackXHsPlus"] = demographics["black"] * demographics["HsPlus"];

  demographics["femaleXHs"] = demographics["female"] * demographics["Hs"];

  demographics["femaleXHsPlus"] =
    demographics["female"] * demographics["HsPlus"];

  return demographics;
};

const getRawScores = (queryObject, appData) => {
  const { instrumentMetadata } = appData;

  const rawScores = {};

  const dois = instrumentMetadata.map((d) => d.doi);

  for (const doi of dois) {
    const instrumentName = instrumentMetadata.find((d) => d.doi === doi)[
      "test"
    ];

    rawScores[instrumentName] = queryObject[doi];
  }

  return rawScores;
};

const getModelScores = (queryObject, appData) => {
  const { instrumentMetadata } = appData;

  const modelScores = {};

  const dois = instrumentMetadata.map((d) => d.doi);

  for (const doi of dois) {
    const modelName = instrumentMetadata.find((d) => d.doi === doi)[
      "modelName"
    ];

    if (["tmta", "tmtb"].includes(doi)) {
      modelScores[modelName] = -1 * Math.log(queryObject[doi]);
    } else {
      modelScores[modelName] = queryObject[doi];
    }
  }

  return modelScores;
};

const getMissing = (queryObject, appData) => {
  const { instrumentMetadata } = appData;

  const dois = instrumentMetadata.map((d) => d.doi);
  const missing = [];

  for (const doi of dois) {
    const keyForResult = `${doi}_result`;

    if (queryObject[keyForResult] === 2) {
      missing.push(doi);
    }
  }

  return missing;
};
