import * as d3 from "d3";
// import { toast } from "materialize-css/dist/js/materialize.min.js";

const drawDiscreteInstrument = (
  svg,
  dataForDiscreteInstrument,
  clickCount,
  dispatch
) => {
  svg.selectAll("*").remove();

  // Unpack Data
  const { dataFromEveryone, dataFromNormativeSample, rawScore } =
    dataForDiscreteInstrument;

  // TODO: Float up to config
  const discretePlotConfiguration = {
    margins: { top: 70, left: 100, bottom: 35, right: 20 },
    height: 270,
    width: 350,
    radius: 0,
    yPadding: 2,
  };

  const { margins } = discretePlotConfiguration;

  // Setup viz and scales
  const { xScale, yScale, zScale, yDomain } = setupVizAndScales(
    svg,
    dataFromEveryone,
    dataFromNormativeSample,
    discretePlotConfiguration
  );

  // Draw Reference Line for Raw Score

  const lineGroup = svg.append("g");
  const pointGroup = svg.append("g");

  pointGroup
    .append("circle")
    .attr("cx", xScale(rawScore))
    .attr("cy", margins.top - yScale.step())
    .attr("r", 4)
    .attr("fill", "steelblue")
    .attr("stroke", "black")
    .attr("opacity", 1)
    .attr("stroke-width", 1);

  lineGroup
    .append("line")
    .attr("x1", xScale(rawScore))
    .attr("x2", xScale(rawScore))
    .attr("y1", yScale("MCSA"))
    .attr("y2", margins.top - yScale.step())
    .attr("stroke-width", 1)
    .style("stroke", "black") // colour the line
    .style("stroke-dasharray", "4");

  // Begin by drawing paths for both groups

  const groups = {};

  groups.everyonePathGroup = svg.append("g");
  groups.normativePathGroup = svg.append("g");

  if (clickCount % 3 === 0) {
    drawPath(
      groups.everyonePathGroup,
      dataFromEveryone,
      "steelblue",
      xScale,
      yScale,
      zScale,
      yDomain
    );
    drawPath(
      groups.normativePathGroup,
      dataFromNormativeSample,
      "peachpuff",
      xScale,
      yScale,
      zScale,
      yDomain
    );

    if (clickCount !== 0) {
      dispatch({
        type: "SET_SNACKBAR_MESSAGE",
        payload: "Showing both samples",
      });
      dispatch({
        type: "OPEN_SNACKBAR",
      });
    }
  } else if (clickCount % 3 === 1) {
    drawPath(
      groups.everyonePathGroup,
      dataFromEveryone,
      "steelblue",
      xScale,
      yScale,
      zScale,
      yDomain
    );
    dispatch({
      type: "SET_SNACKBAR_MESSAGE",
      payload: "Showing the Everyone sample",
    });
    dispatch({
      type: "OPEN_SNACKBAR",
    });
  } else {
    drawPath(
      groups.normativePathGroup,
      dataFromNormativeSample,
      "peachpuff",
      xScale,
      yScale,
      zScale,
      yDomain
    );
    dispatch({
      type: "SET_SNACKBAR_MESSAGE",
      payload: "Showing the Normative sample",
    });
    dispatch({
      type: "OPEN_SNACKBAR",
    });
  }
};

// Helpers for Plot
const getXDomain = (data) => {
  const values = data.map((d) => d.value);
  const uniqueValues = [...new Set(values)];
  uniqueValues.sort((a, b) => a - b);
  return uniqueValues;
};

const getYDomain = () => {
  return ["POOLED", "ARIC", "GMBI", "NACC", "MCSA"];
};

const getZDomain = (dataFromEveryone, dataFromNormativeSample) => {
  const proportions1 = dataFromEveryone.map((d) => d.prop);
  const proportions2 = dataFromNormativeSample.map((d) => d.prop);
  return [0, Math.max(...proportions1, ...proportions2)];
};

const drawAxesForStudiesWithoutData = (
  dataFromEveryone,
  svg,
  xScale,
  yScale,
  xDomain,
  yDomain
) => {
  // Draw Axes for studies without data
  const studiesWithData = [...new Set(dataFromEveryone.map((d) => d.study))];
  for (const el of yDomain) {
    if (!studiesWithData.includes(el)) {
      svg
        .append("line")
        .attr("x1", xScale(Math.min(...xDomain)))
        .attr("x2", xScale(Math.max(...xDomain)))
        .attr("y1", yScale(el))
        .attr("y2", yScale(el))
        .attr("stroke", "black");
    }
  }
};

const setupVizAndScales = (
  svg,
  dataFromEveryone,
  dataFromNormativeSample,
  discretePlotConfiguration
) => {
  const {
    height,
    width,
    yPadding,

    margins,
  } = discretePlotConfiguration;
  // draw graph using data from everyone
  // draw data from normative sample
  // draw raw score

  const xDomain = getXDomain(dataFromEveryone);
  const yDomain = getYDomain();
  const zDomain = getZDomain(dataFromEveryone, dataFromNormativeSample);

  svg.attr("width", width);
  svg.attr("height", height);

  const xAxisGroup = svg.append("g");
  const yAxisGroup = svg.append("g");

  const xScale = d3.scalePoint();
  xScale.domain(xDomain);
  xScale.range([margins.left, width - margins.right]);

  const yScale = d3.scalePoint();
  yScale.domain(yDomain);
  yScale.range([margins.top, height - margins.bottom]);

  const zScale = d3.scaleLinear();
  zScale.domain(zDomain);
  zScale.range([0, yScale.step() - yPadding]);

  let tickValues = [];

  if (xDomain.length > 20) {
    for (let i = 0; i < xDomain.length; i++) {
      if (i % 2 === 0) {
        tickValues.push(xDomain[i]);
      }
    }
  } else {
    tickValues = xDomain;
  }

  const xAxis = (g) =>
    g
      .attr("transform", `translate(0,${height - margins.bottom})`)
      .call(d3.axisBottom(xScale).tickValues(tickValues).tickSizeOuter(0));

  const yAxis = (g) =>
    g
      .attr("transform", `translate(${margins.left},0)`)
      .call(d3.axisLeft(yScale).tickSize(0).tickPadding(4))
      .call((g) => g.select(".domain").remove());

  xAxisGroup.call(xAxis);
  yAxisGroup.call(yAxis);

  drawAxesForStudiesWithoutData(
    dataFromEveryone,
    svg,
    xScale,
    yScale,
    xDomain,
    yDomain
  );

  return { svg, xScale, yScale, zScale, xDomain, yDomain };
};

const drawPath = (
  svgGroup,
  dataGroup,
  color,
  xScale,
  yScale,
  zScale,
  yDomain
) => {
  // Draw Points
  svgGroup
    .append("g")
    .attr("fill", "black")
    .selectAll("circle")
    .data(dataGroup)
    .join("circle")
    .attr("cx", (d) => xScale(d.value))
    .attr("cy", (d) => yScale(d.study) - zScale(d.prop))
    .attr("r", 2);

  // Draw "paths"
  const lineGenerator = d3.line();

  for (const el of yDomain) {
    const filteredToYVal = dataGroup.filter((d) => d.study === el);
    filteredToYVal.sort((a, b) => a.value - b.value);

    // Get data for path on top of figure

    const lineData = filteredToYVal.map((d) => [
      xScale(d.value),
      yScale(d.study) - zScale(d.prop),
    ]);

    // Draw Path on top of the figure
    svgGroup
      .append("path")
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("opacity", 0.25)
      .attr("opacity", 1)
      .attr("stroke-width", 1)
      .attr("d", lineGenerator(lineData));

    // Get data for path around figure

    if (filteredToYVal.length > 0) {
      const xMin = filteredToYVal[0].value;
      const xMax = filteredToYVal[filteredToYVal.length - 1].value;
      lineData.push([xScale(xMax), yScale(filteredToYVal[0].study)]);
      lineData.push([xScale(xMin), yScale(filteredToYVal[0].study)]);
    }

    // Draw path around figure and fill it in
    svgGroup
      .append("path")
      .attr("fill", color)
      .attr("opacity", 0.4)
      .attr("d", lineGenerator(lineData));
  }
};

export default drawDiscreteInstrument;
