const initialState = {
  snackbarMessage: "",
  snackbarOpen: false,
};

export default function reducer(state = initialState, action) {
  switch (action.type) {
    case "SET_SNACKBAR_MESSAGE":
      return { ...state, snackbarMessage: action.payload };

    case "OPEN_SNACKBAR":
      return { ...state, snackbarOpen: true };
    case "CLOSE_SNACKBAR":
      return { ...state, snackbarOpen: false };
    default:
      return state;
  }
}
