const getInstrumentChartData = (participant, appData) => {
  const { instruments } = appData;
  const instrumentChartData = {};

  for (const instrument of instruments) {
    instrumentChartData[instrument] = getDataForInstrumentChart(
      participant,
      instrument,
      appData
    );
  }
  return instrumentChartData;
};

const getDataForInstrumentChart = (participant, instrument, appData) => {
  const { instrumentMetadata, summariesOfRawScores } = appData;

  const everyonePlotData = summariesOfRawScores["everyone"];
  const normativePlotData = summariesOfRawScores["normative"];

  const rawScore = participant["rawScores"][instrument];

  const continuousInstruments = instrumentMetadata
    .filter((d) => d.type === "continuous")
    .map((d) => d.test);

  const discreteInstruments = instrumentMetadata
    .filter((d) => d.type === "discrete")
    .map((d) => d.test);

  let dataFromEveryone, dataFromNormativeSample;

  if (continuousInstruments.includes(instrument)) {
    dataFromEveryone = everyonePlotData["continuous"][instrument];
    dataFromNormativeSample = normativePlotData["continuous"][instrument];
  } else if (discreteInstruments.includes(instrument)) {
    dataFromEveryone = everyonePlotData["discrete"][instrument];
    dataFromNormativeSample = normativePlotData["discrete"][instrument];
  }

  return { rawScore, dataFromEveryone, dataFromNormativeSample };
};

export default getInstrumentChartData;
