import { Fragment, useState, useEffect } from "react";

import QueryStringCollectionForm from "./Components/QueryStringCollectionForm";
import WidgetForm from "./Components/WidgetForm";
import MainProgram from "./Components/MainProgram";

import { destructureQueryParams } from "./helpers/destructureQueryParams";

import CustomSnackBar from "./Components/CustomSnackbar";

function App() {
  /**
   *
   * We will need to use some State to help figure out when we can start the main program.
   * We can start the main program if and only if information about the particiapnt
   * has been provided either from the URL or from the text box.
   *
   * We will check to see if a query string has been provided in the URL
   * and store the results in a queryObject.
   * If a query string has not been provided, then queryObject will initially be empty.
   *
   * We will want to be able to change both
   * - the state of queryObject
   *     so that it can be updated if nothing was provided in the URL
   * - the state of the "queryStringHasBeenProvided" boolean
   *    so that we can begin the main program once something has been provided   *
   */

  /**
   *
   * Begin the "first-pass"
   *
   * */

  // Check to see if a query string has been provided in the URL
  const queryStringFromURL = window.location.search;

  // Attempt to get the queryObject from the queryStringFromURL
  const { queryObjectFromURL, numParamsFoundInURL } =
    destructureQueryParams(queryStringFromURL);

  // Store attempt to create queryObject,
  // allowing for changes later on if unsuccessful on first pass
  const [queryObject, setQueryObject] = useState(queryObjectFromURL);

  // Check to see if it was successful on first pass
  const [queryStringHasBeenProvided, setQueryStringHasBeenProvided] = useState(
    numParamsFoundInURL > 0
  );

  // Done with first pass.

  /**
   *
   * Respond when the state of the queryObject changes.
   *
   */

  useEffect(() => {
    if (queryObject) {
      if (Object.keys(queryObject).length > 20) {
        setQueryStringHasBeenProvided(true);
      }
    }
  }, [queryObject]);

  /**
   *
   * Begin the main program if a queryStringHasBeenProvided.
   * Otherwise, render the QueryStringCollectionForm so that we can collect a query string.
   *
   * */

  if (queryStringHasBeenProvided) {
    return (
      <Fragment>
        <MainProgram queryObject={queryObject}></MainProgram>
        <CustomSnackBar></CustomSnackBar>
      </Fragment>
    );
  } else {
    return (
      <Fragment>
        <QueryStringCollectionForm
          setQueryObject={setQueryObject}
        ></QueryStringCollectionForm>
        <CustomSnackBar></CustomSnackBar>
        <WidgetForm setQueryObject={setQueryObject}></WidgetForm>
      </Fragment>
    );
  }
}

export default App;
