import { useRef, useEffect, useState } from "react";

import { select } from "d3";

import { useDispatch } from "react-redux";

import drawContinuousInstrument from "../../helpers/visualizations/drawContinuousInstrument";

const ContinuousInstrumentViz = (props) => {
  const dispatch = useDispatch();
  const { continousInstrumentPlotData } = props;

  const [clickCount, setCount] = useState(0);

  const handleClick = () => {
    setCount(clickCount + 1);
  };

  const svgRef = useRef();

  useEffect(() => {
    const svg = select(svgRef.current);
    drawContinuousInstrument(
      svg,
      continousInstrumentPlotData,
      clickCount,
      dispatch
    );
  }, [continousInstrumentPlotData, clickCount, dispatch]);

  return <svg ref={svgRef} onClick={handleClick}></svg>;
};

export default ContinuousInstrumentViz;
