import React, { useRef, useEffect } from "react";

import { select, range, scaleLinear, max, area, curveBasis } from "d3";

function Plot({
  residualPlotData,
  nameOfInstrumentOrDomain,
  labelOfInstrumentOrDomain,
  height,
  marginTop,
  marginLeft,
  marginRight,
  marginBottom,
  width,
}) {
  const svgRef = useRef();

  useEffect(() => {
    const svg = select(svgRef.current);

    const exampleData = residualPlotData[nameOfInstrumentOrDomain];
    const { ridgelineData, zScores } = exampleData;

    const { dates, series } = ridgelineData;
    const { pooled } = zScores;
    const zScore = pooled;

    let xValues = dates;
    let yValues = series.find((d) => d.name === "pooled")["values"];

    xValues = [xValues[0], ...xValues, xValues[xValues.length - 1]];
    yValues = [0, ...yValues, 0];

    const I = range(xValues.length);

    /*
Draw plot
*/

    const radiusSize = Math.sqrt(height) / 2;

    const x = scaleLinear()
      .domain([-4.5, 4.5])
      .range([marginLeft, width - marginRight]);

    const y = scaleLinear()
      .domain([0, max(yValues)])
      .nice()
      .range([height - marginBottom, marginTop]);

    const areaFunction = area()
      .curve(curveBasis)
      .x((i) => x(xValues[i]))
      .y0(0)
      .y1((i) => y(yValues[i]));

    const line = areaFunction.lineY1();

    svg.attr("height", height);
    svg.attr("width", width);

    svg
      .append("path")
      .attr("fill", "#fff")
      .attr("stroke", "black")
      .attr("d", line(I));

    // Add marker

    const lineGroup = svg.append("g");
    const pointGroup = svg.append("g");

    pointGroup
      .append("circle")
      .attr("cx", x(zScore))
      .attr("cy", marginTop)
      .attr("r", radiusSize)
      .attr("fill", "steelblue")
      .attr("stroke", "black")
      .attr("opacity", 1)
      .attr("stroke-width", 1);

    lineGroup
      .append("line")
      .attr("x1", x(zScore))
      .attr("x2", x(zScore))
      .attr("y1", y(0))
      .attr("y2", marginTop)
      .attr("stroke-width", 1)
      .style("stroke", "black") // colour the line
      .style("stroke-dasharray", "2");

    const textGroup = svg.append("g");
    textGroup
      .append("text")
      .attr("y", 10)
      .attr("x", width / 2)
      .style("font-size", "12px")
      .style("fill", "white")
      .style("text-anchor", "middle")
      .text(labelOfInstrumentOrDomain);

    textGroup
      .append("text")
      .attr("y", height)
      .attr("x", x(zScore))
      .style("font-size", "12px")
      .style("text-anchor", "middle")
      .style("fill", "white")
      .text(Math.round(zScore * 100) / 100);
  }, [
    height,
    labelOfInstrumentOrDomain,
    marginBottom,
    marginLeft,
    marginRight,
    marginTop,
    nameOfInstrumentOrDomain,
    residualPlotData,
    width,
  ]);

  return (
    <div>
      <svg ref={svgRef}></svg>
    </div>
  );
}

export default Plot;
