const getResidualPlotData = (report, appData) => {
  const { domains, instruments } = appData;
  const residualPlotData = {};

  for (const domain of domains) {
    residualPlotData[domain] = getDataForResidualPlot(domain, report, appData);
  }

  for (const instrument of instruments) {
    residualPlotData[instrument] = getDataForResidualPlot(
      instrument,
      report,
      appData
    );
  }

  return residualPlotData;
};

const getDataForResidualPlot = (key, report, appData) => {
  const ridgelineData = getRidgelineDataForResidualPlot(key, appData);
  const zScores = getZScores(key, report, appData);
  return { ridgelineData, zScores };
};

const getZScores = (key, report, appData) => {
  const domainOrInstrument = determineIfDomainOrInstrument(key, appData);
  if (domainOrInstrument === "domain") {
    return report["domainLevelZScores"][key];
  } else if (domainOrInstrument === "instrument") {
    return report["instrumentLevelZScores"][key];
  }
};

const getRidgelineDataForResidualPlot = (key, appData) => {
  const {
    domainLevelResidualsStandardized,
    instrumentLevelResidualsStandardized,
  } = appData;
  const domainLevelResiduals = domainLevelResidualsStandardized;
  const instrumentLevelResiduals = instrumentLevelResidualsStandardized;
  const domainOrInstrument = determineIfDomainOrInstrument(key, appData);
  if (domainOrInstrument === "domain") {
    return domainLevelResiduals[key];
  } else if (domainOrInstrument === "instrument") {
    let newKey = key;
    if (key === "tmta") {
      newKey = "model_tmta";
    } else if (key === "tmtb") {
      newKey = "model_tmtb";
    }
    return instrumentLevelResiduals[newKey];
  }
};

const determineIfDomainOrInstrument = (key, appData) => {
  const { domains, instruments } = appData;
  if (domains.includes(key)) {
    return "domain";
  } else if (instruments.includes(key)) {
    return "instrument";
  }
};

export default getResidualPlotData;
