import { useState } from "react";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import { Stack } from "@mui/material";
import { Box } from "@mui/material";

import Plot from "./Plot";

import { colors } from "./Colors";
const { RED, ORANGE, GREEN, RED_LIGHT, ORANGE_LIGHT, GREEN_LIGHT } = colors;

const getBackgroundColorFromZScore = (zScore, isHovering) => {
  if (isHovering) {
    if (zScore < -1.5) {
      return RED_LIGHT;
    } else if (zScore < -0.5) {
      return ORANGE_LIGHT;
    } else {
      return GREEN_LIGHT;
    }
  }
  if (zScore < -1.5) {
    return RED;
  } else if (zScore < -0.5) {
    return ORANGE;
  } else {
    return GREEN;
  }
};

export default function DomainCard({
  nameOfDomain,
  labelForDomain,
  domainLevelZScores,
  residualPlotData,
  plotHeight,
  plotWidth,
  marginTop,
  marginLeft,
  marginRight,
  marginBottom,
  cardHeight,
  cardRelativeWidth,
  domainRef,
  scrollToRef,
}) {
  const [isHovering, setIsHovering] = useState(false);
  return (
    <Card
      sx={{
        width: cardRelativeWidth,
        height: cardHeight,
        background: getBackgroundColorFromZScore(
          domainLevelZScores[nameOfDomain]["pooled"],
          isHovering
        ),
        color: "white",
      }}
      style={{ cursor: "pointer" }}
      onMouseEnter={() => setIsHovering(true)}
      onMouseLeave={() => setIsHovering(false)}
    >
      <CardContent>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
          onClick={() => {
            scrollToRef(domainRef);
          }}
        >
          <Stack sx={{ alignItems: "center" }}>
            <Plot
              residualPlotData={residualPlotData}
              nameOfInstrumentOrDomain={nameOfDomain}
              labelOfInstrumentOrDomain={labelForDomain}
              height={plotHeight}
              width={plotWidth}
              marginTop={marginTop}
              marginLeft={marginLeft}
              marginRight={marginRight}
              marginBottom={marginBottom}
            ></Plot>
          </Stack>
        </Box>
      </CardContent>
    </Card>
  );
}
