import * as d3 from "d3";
// import { toast } from "materialize-css/dist/js/materialize.min.js";

const drawContinuousInstrument = (
  svg,
  dataForContinuousInstrument,
  clickCount,
  dispatch
) => {
  svg.selectAll("*").remove();
  const { dataFromEveryone, rawScore, dataFromNormativeSample } =
    dataForContinuousInstrument;

  // TODO: Float up to config
  const continuousInstrumentConfiguration = {
    overlap: 1,
    ceiling: 25,
    height: 280,
    width: 350,
    margin: { top: 40, right: 20, bottom: 30, left: 70 },
  };

  const { overlap, ceiling, height, width, margin } =
    continuousInstrumentConfiguration;

  // Setup Scales

  const minX1 = d3.min(dataFromEveryone.dates);
  const minX2 = d3.min(dataFromNormativeSample.dates);
  const maxX1 = d3.max(dataFromEveryone.dates);
  const maxX2 = d3.max(dataFromNormativeSample.dates);

  const x = d3
    .scaleLinear()
    .domain([Math.min(minX1, minX2), Math.max(maxX1, maxX2)])
    .range([margin.left, width - margin.right]);

  const y = d3
    .scalePoint()
    .domain(dataFromEveryone.series.map((d) => d.name))
    .range([margin.top + ceiling, height - margin.bottom]);

  const max1 = d3.max(dataFromEveryone.series, (d) => d3.max(d.values));
  const max2 = d3.max(dataFromNormativeSample.series, (d) => d3.max(d.values));

  const z = d3
    .scaleLinear()
    .domain([0, Math.max(max1, max2)])
    .nice()
    .range([0, -overlap * y.step()]);

  // Setup Axes

  const xAxis = (g) =>
    g.attr("transform", `translate(0,${height - margin.bottom})`).call(
      d3
        .axisBottom(x)
        .ticks(width / 80)
        .tickSizeOuter(0)
    );

  const yAxis = (g) =>
    g
      .attr("transform", `translate(${margin.left},0)`)
      .call(d3.axisLeft(y).tickSize(0).tickPadding(4))
      .call((g) => g.select(".domain").remove());

  // Setup area and line helpers

  const everyoneArea = d3
    .area()
    .curve(d3.curveBasis)
    .defined((d) => !isNaN(d))
    .x((d, i) => x(dataFromEveryone.dates[i]))
    .y0(0)
    .y1((d) => z(d));

  const everyoneLine = everyoneArea.lineY1();

  const normativeArea = d3
    .area()
    .curve(d3.curveBasis)
    .defined((d) => !isNaN(d))
    .x((d, i) => x(dataFromNormativeSample.dates[i]))
    .y0(0)
    .y1((d) => z(d));

  const normativeLine = normativeArea.lineY1();

  // Setup SVG

  svg.attr("height", height);
  svg.attr("width", width);

  // Draw Axes

  svg.append("g").call(xAxis);
  svg.append("g").call(yAxis);

  // Setup Groups
  const groups = {};

  // Define Helpers to draw things

  const drawEveryoneGroup = (groups, everyoneArea, everyoneLine) => {
    groups.everyoneGroup = svg
      .append("g")
      .selectAll("g")
      .data(dataFromEveryone.series)
      .join("g")
      .attr("transform", (d) => `translate(0,${y(d.name) + 1})`);

    groups.everyoneGroup
      .append("path")
      .attr("fill", "steelblue")
      .attr("opacity", 0.4)
      .attr("d", (d) => everyoneArea(d.values));

    groups.everyoneGroup
      .append("path")
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("d", (d) => everyoneLine(d.values));
  };

  const drawNormativeGroup = () => {
    groups.normativeGroup = svg
      .append("g")
      .selectAll("g")
      .data(dataFromNormativeSample.series)
      .join("g")
      .attr("transform", (d) => `translate(0,${y(d.name) + 1})`);
    groups.normativeGroup
      .append("path")
      .attr("fill", "peachpuff")
      .attr("opacity", 0.2)
      .attr("d", (d) => normativeArea(d.values));

    groups.normativeGroup
      .append("path")
      .attr("fill", "none")
      .attr("stroke", "black")
      .attr("id", "someone")
      .attr("d", (d) => normativeLine(d.values));
  };

  if (clickCount % 3 === 0) {
    drawEveryoneGroup(groups, everyoneArea, everyoneLine);
    drawNormativeGroup(groups, normativeArea, normativeLine);
    if (clickCount !== 0) {
      // toast({ html: "Showing both samples" });
      // toast({ html: "Showing both samples" });
      // setSnackbarMessage("Showing both samples");
      // setSnackbarOpen(true);

      dispatch({
        type: "SET_SNACKBAR_MESSAGE",
        payload: "Showing both samples",
      });
      dispatch({
        type: "OPEN_SNACKBAR",
      });
    }
  } else if (clickCount % 3 === 1) {
    drawEveryoneGroup(groups, everyoneArea, everyoneLine);
    //  toast({ html: "Showing the Everyone sample" });
    // toast({ html: "Showing the Everyone sample" });
    // setSnackbarMessage("Showing the Everyone sample");
    // setSnackbarOpen(true);
    dispatch({
      type: "SET_SNACKBAR_MESSAGE",
      payload: "Showing the Everyone sample",
    });
    dispatch({
      type: "OPEN_SNACKBAR",
    });
  } else {
    drawNormativeGroup(groups, normativeArea, normativeLine);
    // toast({ html: "Showing the Normative sample" });
    // toast({ html: "Showing the Normative sample" });
    // setSnackbarMessage("Showing the Normative sample");
    // setSnackbarOpen(true);
    dispatch({
      type: "SET_SNACKBAR_MESSAGE",
      payload: "Showing the Normative sample",
    });
    dispatch({
      type: "OPEN_SNACKBAR",
    });
  }

  // Draw Reference Line for Raw Score

  const lineGroup = svg.append("g");
  const pointGroup = svg.append("g");

  pointGroup
    .append("circle")
    .attr("cx", x(rawScore))
    .attr("cy", margin.top)
    .attr("r", 4)
    .attr("fill", "steelblue")
    .attr("stroke", "black")
    .attr("opacity", 1)
    .attr("stroke-width", 1);

  lineGroup
    .append("line")
    .attr("x1", x(rawScore))
    .attr("x2", x(rawScore))
    .attr("y1", y("MCSA"))
    .attr("y2", margin.top)
    .attr("stroke-width", 1)
    .style("stroke", "black") // colour the line
    .style("stroke-dasharray", "4");
};

export default drawContinuousInstrument;
