import { useRef, useEffect } from "react";
import { select } from "d3";

import drawResidualRidgelines from "../../helpers/visualizations/drawResidualRidgelines";

const ResidualRidgelinesViz = (props) => {
  const { resPlotData } = props;

  const svgRef = useRef();

  useEffect(() => {
    const svg = select(svgRef.current);
    drawResidualRidgelines(svg, resPlotData);
  }, [resPlotData]);

  return <svg ref={svgRef}></svg>;
};

export default ResidualRidgelinesViz;
