import * as React from "react";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import Stack from "@mui/material/Stack";

import appData from "../../data/appData.json";
import ResidualRidgelinesViz from "./ResidualRidgelinesViz";

import DiscreteInstrumentViz from "./DiscreteInstrumentViz";
import ContinuousInstrumentViz from "./ContinuousInstrumentViz";

import ListOfZScores from "./ListOfZScores";

export default function BasicCard({
  instrumentName,
  reportData,
  participant,
  domainLabel,
}) {
  // What do we need to show a row in the instrument table?
  const { report, instrumentChartData, residualPlotData } = reportData;

  const { instrumentLevelZScores } = report;
  const { rawScores } = participant;
  const { instrumentMetadata } = appData;

  const metadataForInstrument = instrumentMetadata.find(
    (d) => d.test === instrumentName
  );

  const label = metadataForInstrument["label"];

  const continuousInstrument = metadataForInstrument["type"] === "continuous";
  const rawScore = rawScores[instrumentName];
  const zScores = instrumentLevelZScores[instrumentName];
  const chartData = instrumentChartData[instrumentName];
  const resPlotData = residualPlotData[instrumentName];

  return (
    <Card>
      <CardContent>
        <Stack spacing={2}>
          <Typography variant="h4">{label}</Typography>
          <Typography variant="caption">{domainLabel} Test</Typography>

          <Stack
            direction="row"
            spacing={2}
            justifyContent="center"
            alignItems="center"
          >
            <Card>
              <CardContent>
                <Typography variant="h5">Raw score: {rawScore}</Typography>
                {continuousInstrument ? (
                  <ContinuousInstrumentViz
                    continousInstrumentPlotData={chartData}
                  ></ContinuousInstrumentViz>
                ) : (
                  <DiscreteInstrumentViz
                    discreteInstrumentPlotData={chartData}
                  ></DiscreteInstrumentViz>
                )}
              </CardContent>
            </Card>

            <Card>
              <CardContent>
                <Typography variant="h5">zScores by Study</Typography>
                <Stack
                  direction="row"
                  spacing={2}
                  justifyContent="center"
                  alignItems="center"
                >
                  <ListOfZScores zScores={zScores}></ListOfZScores>
                  <ResidualRidgelinesViz
                    resPlotData={resPlotData}
                  ></ResidualRidgelinesViz>
                </Stack>
              </CardContent>
            </Card>
          </Stack>
        </Stack>
      </CardContent>
    </Card>
  );
}
