const getQueryObject = (inputText) => {
  const noEmptySpace = inputText.replace(/\s/g, "");

  const noLineBreaks = noEmptySpace.replace(/(\r\n|\n|\r)/gm, "");

  const noSpace = noLineBreaks.replace(" ", "");
  const splitLines = noSpace.split(",");
  const participant = {};

  for (const feature of splitLines) {
    const splitFeature = feature.split("=");
    const featureName = splitFeature[0];
    const featureValue = splitFeature[1];
    if (featureName !== "missing") {
      participant[featureName] = parseInt(featureValue);
    } else {
      participant["missing"] = featureValue;
    }
  }

  return participant;
};

export { getQueryObject };
