import React from "react";
import { useState } from "react";

import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import { Stack } from "@mui/material";
import { Box } from "@mui/material";

import Plot from "./Plot";

import { colors } from "./Colors";
const { RED, ORANGE, GREEN, RED_LIGHT, ORANGE_LIGHT, GREEN_LIGHT } = colors;

const getBackgroundColorFromZScore = (zScore, isHovering) => {
  if (isHovering) {
    if (zScore < -1.5) {
      return RED_LIGHT;
    } else if (zScore < -0.5) {
      return ORANGE_LIGHT;
    } else {
      return GREEN_LIGHT;
    }
  }
  if (zScore < -1.5) {
    return RED;
  } else if (zScore < -0.5) {
    return ORANGE;
  } else {
    return GREEN;
  }
};
/*

                  nameOfInstrument={"tmta"}
                          labelForInstrument={"Trails A"}
                          instrumentLevelZScores={instrumentLevelZScores}
                          residualPlotData={residualPlotData}
                          plotHeight={70}
                          plotWidth={100}
                          marginTop={25}
                          marginLeft={10}
                          marginRight={10}
                          marginBottom={20}
                          cardHeight={100}
                          cardRelativeWidth={1}

                          */
function InstrumentCard({
  nameOfInstrument,
  labelForInstrument,
  instrumentLevelZScores,
  residualPlotData,
  plotHeight,
  plotWidth,
  marginTop,
  marginLeft,
  marginRight,
  marginBottom,
  cardHeight,
  cardRelativeWidth,
  instrumentRef,
  scrollToRef,
}) {
  const [isHovering, setIsHovering] = useState(false);
  return (
    <Card
      sx={{
        width: cardRelativeWidth,
        background: getBackgroundColorFromZScore(
          instrumentLevelZScores[nameOfInstrument]["pooled"],
          isHovering
        ),
        color: "white",
        padding: 0,
        m: 0,
        height: cardHeight,
      }}
      style={{ cursor: "pointer" }}
      onMouseEnter={() => setIsHovering(true)}
      onMouseLeave={() => setIsHovering(false)}
    >
      <CardContent>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
          }}
          onClick={() => {
            scrollToRef(instrumentRef);
          }}
        >
          <Stack
            direction="row"
            sx={{
              width: 1,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Plot
              residualPlotData={residualPlotData}
              nameOfInstrumentOrDomain={nameOfInstrument}
              labelOfInstrumentOrDomain={labelForInstrument}
              height={plotHeight}
              width={plotWidth}
              marginTop={marginTop}
              marginLeft={marginLeft}
              marginRight={marginRight}
              marginBottom={marginBottom}
            ></Plot>
          </Stack>
        </Box>
      </CardContent>
    </Card>
  );
}

export default InstrumentCard;
