const destructureQueryParams = (queryStringFromURL) => {
  // Look at a query string.

  // Return
  //   - a parsed queryObject and
  //   - the number of parameters in the query string

  let numParamsFoundInURL = 0;

  const queryParams = new URLSearchParams(queryStringFromURL);
  const queryObjectFromURL = {};

  for (const [key, value] of queryParams) {
    numParamsFoundInURL += 1;
    queryObjectFromURL[key] = parseInt(value);
  }

  return { queryObjectFromURL, numParamsFoundInURL };
};

export { destructureQueryParams };
