import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Stack from "@mui/material/Stack";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";

import { colors } from "./Colors";
const { RED, ORANGE, GREEN } = colors;

const getBackgroundColorFromClassification = (classification) => {
  if (classification === "Dementia") {
    return RED;
  } else if (classification === "MCI") {
    return ORANGE;
  } else {
    return GREEN;
  }
};

export default function OverallClassification({ classification }) {
  return (
    <Card
      sx={{
        background: getBackgroundColorFromClassification(classification),
        color: "white",
      }}
    >
      <CardContent>
        <Box>
          <Stack
            sx={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Typography variant="h4">NCB Classification</Typography>
            <Typography variant="h5">{classification}</Typography>
          </Stack>
        </Box>
      </CardContent>
    </Card>
  );
}
